import Vue from 'vue';
import Vuex from 'vuex';
// import axios from 'axios';

import productsModules from './products';
import cartModules from './cart';
// import modalModules from './modal'
Vue.use(Vuex);

export default new Vuex.Store({
    strict: true, //嚴謹模式

    state: {   //可以當成data
        isLoading: false,
    },

    actions: {  // actions 是操作行為   context固定   可以當成methods
        updateLoading(context, status) {   //payload  載荷  給他一個名稱為status
            context.commit('LOADING', status);
        },
    },

    mutations: {  //mutations 是操作狀態  需用常數所以命名為大寫  state固定,代表資料  status 為payload
        LOADING(state, status) {
            state.isLoading = status;
        },

  
    },

    getters:{  //可以當成computed
        isLoading:state=>state.isLoading,
    },

    modules:{
        productsModules,
        cartModules,
        // modalModules
        
    },

});