import axios from 'axios';

export default {
    //state 屬於模組區域變數
    //actions, mutations, getters 是屬於全域變數
    namespaced: true,  //加上namespaced:true   actions, mutations, getters 會變成區域變數
    state: {   //可以當成data
        products: [],
        categories: [],

    },

    actions: {  //context固定   可以當成methods
        getProducts(context) {
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`;
            context.commit('LOADING', true, { root: true });  //root: true 代表全域
            axios.get(url).then(response => {
                context.commit('PRODUCTS', response.data.products);
                context.commit('CATEGORIES', response.data.products);
                console.log("取得產品列表:", response);
                context.commit('LOADING', false, { root: true });  //root: true 代表全域
            });
        },



    },

    mutations: {  //mutations 需用常數所以命名為大寫  state固定,代表資料  

        PRODUCTS(state, payload) {
            state.products = payload;
        },

        CATEGORIES(state, payload) {
            const categories = new Set();
            payload.forEach(item => {
                categories.add(item.category);
            });
            state.categories = Array.from(categories);
        },


    },

    getters: {  //可以當成computed
        categories(state) {
            return state.categories;
        },
        products(state) {
            return state.products;
        },


    }
}