import axios from 'axios';

export default {
     //state 屬於模組區域變數
    //actions, mutations, getters 是屬於全域變數
    namespaced: true,  //加上namespaced:true   actions, mutations, getters 會變成區域變數
    state: {   //可以當成data    
        cart: {
            carts: {}
        },
        status: {
            loadingItem: ""
        },
    },

    actions: {  //context固定   可以當成methods
        getCart(context) {
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
            context.commit('LOADING', true, {root: true});
            axios.get(url).then(response => {
                if (response.data.data.carts) {
                    context.commit('CART', response.data.data);
                }
                context.commit('LOADING', false, {root: true});
                console.log('取得購物車', response.data.data)
            });
        },

       

        removeCart(context, id) {
          
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
            context.commit('LOADING', true, {root: true});
            axios.delete(url).then(response => {
                context.commit('LOADING', false, {root: true});
                context.dispatch('getCart')
                console.log("刪除購物車項目", response);
            });
        },

    },

    mutations: {  //mutations 需用常數所以命名為大寫  state固定,代表資料  status 為payload
   

        CART(state, payload) {
            state.cart = payload;
        }
    },

    getters:{  //可以當成computed

        cart(state) {
            return state.cart;
          }
    },

   
}